body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  min-width: 100vh;
  min-height: 100vh;
}

main {
  min-height: 100vh;
  min-width: 80%;
  max-height: 100%;
  max-width: 80%;
  margin: 0 auto;
  background: #fff;
  padding: 0px;
  box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.22);
  background-image: url('./img/RickandMorty.jpg');
  background-size: contain;
}

.grid-view {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.page-view {
  min-height: 40vh;
}

.grid-view div.card.ui {
  margin: 1.25rem;
}
/*
.main-img {
  display: block;
  border-radius: 100%;
  margin: 0 auto;
} */

header h1 {
  text-align: center;
  color: rgb(2, 199, 28);
  background: rgb(0, 0, 0, 0.8)
}

.Char-Container {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin: 0 auto;
  margin-top: 5%;
}
.Card {
  width: 100%;
  background: rgb(255, 255, 255, 0.9) !important;
  border: 1px solid black !important;
}
.Card-Text {
  font-size: 2rem;
  font-weight: 600;
}
.Card-Title {
  font-weight: 700;
  font-size: 4rem;
  text-decoration: underline;
}
.Card-Img {
  max-width: 50%;
  max-height: 50%;
  border: 1px solid black;
}
.button-container {
  display: flex;
  justify-content: center;
}

@media only screen and (max-width: 900px) {
  main {
    min-width: 100%;
    min-height: 100vh;
    max-width: 100vh;
  }
  .Char-Container {
    flex-direction: column;
    margin-left: 1rem;
    margin-right: 1rem;
  }
  .Card-Img {
    max-width: 100%;
    max-height: 70%;
  }
}